import React from 'react';
import { Carousel } from 'primereact/carousel';
import { useNavigate } from 'react-router-dom';
import styles from './CarouselCustomMain.module.css';

export default function CarouselCustomMain({ carouselItems }) {
  const navigate = useNavigate(); // Move useNavigate here

  const responsiveOptions = [
    {
      breakpoint: '1400px',
      numVisible: 3,
      numScroll: 2,
    },
    {
      breakpoint: '1000px',
      numVisible: 2,
      numScroll: 2,
    },
    {
      breakpoint: '780px',
      numVisible: 1,
      numScroll: 1,
    },
  ];

  const carouselTemplate = item => {
    const handleItemClick = e => {
      e.preventDefault(); // Prevent default anchor behavior

      if (item.action === 'scroll' && item.target) {
        const element = document.querySelector(item.target);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }
      } else if (item.link) {
        navigate(item.link);
      }
    };

    return (
      <a
        href={item.link || '#'}
        onClick={handleItemClick}
        className={styles.carouselItem}
      >
        <img src={item.src} alt={item.caption} />
        <div className={styles.carouselCaption}>{item.caption}</div>
      </a>
    );
  };

  return (
    <div className='container' style={{ margin: '40px auto' }}>
      <Carousel
        value={carouselItems}
        itemTemplate={carouselTemplate}
        responsiveOptions={responsiveOptions}
        numVisible={3}
        numScroll={2}
        autoplayInterval={8000}
        circular
      />
    </div>
  );
}
